@layer aksel.baseline {
  body, :host {
    font-family: var(--ax-font-family, "Source Sans 3", "Source Sans Pro", Arial, sans-serif);
    font-size: 1.125rem;
    line-height: 1.333;
  }

  [hidden] {
    display: none;
  }

  @media (prefers-reduced-motion: reduce) {
    :not(.aksel-loader *, .aksel-loader, .aksel-progress-bar *, .aksel-progress-bar), :before, :after {
      background-attachment: initial !important;
      scroll-behavior: auto !important;
      transition-duration: 0s !important;
      transition-delay: 0s !important;
      animation-duration: 1ms !important;
      animation-iteration-count: 1 !important;
      animation-delay: -1ms !important;
    }
  }

  .sr-only, .aksel-sr-only {
    clip: rect(0, 0, 0, 0);
    clip-path: inset(50%);
    white-space: nowrap;
    border: 0;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }

  .sr-only.focusable:active, .sr-only.focusable:focus, .aksel-sr-only.focusable:active, .aksel-sr-only.focusable:focus {
    clip: auto;
    clip-path: none;
    height: auto;
    white-space: inherit;
    width: auto;
    margin: 0;
    position: static;
    overflow: visible;
  }
}

@layer aksel.print {
  @media print {
    *, :before, :after {
      color: #000 !important;
      box-shadow: none !important;
      text-shadow: none !important;
      background: #fff !important;
    }

    a, a:visited {
      text-decoration: underline;
    }

    a[href]:after {
      content: " (" attr(href) ")";
    }

    abbr[title]:after {
      content: " (" attr(title) ")";
    }

    a[href^="#"]:after, a[href^="javascript:"]:after {
      content: "";
    }

    pre {
      white-space: pre-wrap !important;
    }

    pre, blockquote {
      page-break-inside: avoid;
      border: 1px solid #999;
    }

    thead {
      display: table-header-group;
    }

    tr, img {
      page-break-inside: avoid;
    }

    p, h2, h3 {
      orphans: 3;
      widows: 3;
    }

    h2, h3 {
      page-break-after: avoid;
    }
  }
}

